.container{
    display: flex;
    flex-direction: column;
    justify-content:flex-start;
    align-items: center;
    width: 100%;
    height: auto;
  
}
.formContainer{
    width: 100%;
    height:auto;
   display: flex;
   flex-direction: row;
   flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 5%;
    
}

.formBox{
    width: 100%;
    padding: 1rem 4rem;
    background: rgb(250, 250, 250);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    margin: 0.05rem 0rem;
    box-shadow: 1px 1px 1px #aaa;
}
.formBox:nth-child(13){
    width: 100%;
}

.input{
    width:40rem;
    height: 3.8rem;
    margin: 0;
    padding: 0;
    border: 0px;
    box-shadow: 1px 1px 1px #ccc;
    border-radius: 4px;
    padding-left: 2rem;
}

.label{
    width: 100%;
    text-align: left;
    display: block;
    margin: 1rem 0;
}
.button{
    padding: 0.5rem 2.5rem;
    outline: none;
    border: 1px solid #ddd;
    border-radius: 0.5rem;
    margin-top: 2rem;
}
.button:hover{
    box-shadow: 5px 5px 1px #aaa;
}
.next{
    bottom: 1rem;
    right: 1rem;
}
.prev{
    bottom: 1rem;
    left: 1rem;
}
.button:focus{
    outline: none;
}
.optionBox{
    padding: 1rem;
    border: 1px solid #ddd;
    border-radius: 5px;
    margin : 0 0.5rem;
}
.optionContainerRow{
    padding: 2rem;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}
.optionContainerCol{
    padding: 2rem;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    
}
.optionContainerRow label, .optionContainerCol label{
    padding: 1rem;
}

.resultContainer{
    width: 100%;
    height: 80vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.resultContainer h4{
    display: block;
    width: 100%;
    font-size: 1.8rem;
}

.knowledge p{
    text-align: justify;
    font-size: 1.4rem;
    margin: 1rem 0;
    color: #555;
    font-weight: 100;
}