.container{
   width: 100%;
   height: auto;
   margin-top: 16vh;
}
.header{
    width: 100%;
    height: 16vh;
    border-bottom: 1px solid rgb(248, 245, 245);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background: #fff;
    position: fixed;
    top:0;
    left: 0;
    z-index: 6000;
}

.logo{
    width: 30%;
    height: 10rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    color: #3483eb;
}
.logo h2,h4{
    font-weight: 500;
}
.logo h4{
    font-style: italic;
}
.logo  img {
    width: 5rem;
    height: auto;
}
.nav{
    width: 60%;
    height: auto;
    margin: auto auto;   
}

.nav ul{
    width: 40rem;
    height: 4rem;
    list-style-type: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    
}
.nav ul li{
    width: auto;
    height: 4rem;
    margin: auto auto;
    padding: 1rem;
}
.nav ul > li > a{
    width: 100%;
    padding: 0 1rem;
    min-height: 5rem;
    text-decoration: none;
    color:#112;
    font-weight: 300;
}
.nav ul > li:hover a{
    color: #3489eb;
}

.footer{
    width: 100%;
    min-height: 14vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 5rem;
    font-size: 1.4rem;
}