.container{
    width: 100%;
    min-height: 70vh;
}
.background{
    height: 70vh;
    width: 100%;
    background: url('../../Assets//bg-2.png') no-repeat;
    background-size: cover;
}
.home{
    width: 100%;
    height: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.home_left{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 0 5rem;
    padding: 5rem;
}
.content{
    background: rgba(250, 252, 255, 0.8);
    width: 40%;
    padding: 5vh 5rem;
    border-radius: 1rem;
}
.home_left button{
    border: 1px solid #ddd;
    background: #fff;
    padding: 1rem 2rem;
    font-size: 1.5rem;
    margin: 1rem 0;
}
.form{
    width: 30rem;
    height: 40rem;
    background: rgba(209, 219, 230, 0.9);
    padding: 2rem;
}

.inputContainer{
    margin: 1.5rem 0;  
}
.label{
    display: block;
    text-align: left;
    padding: 1px 0;
    font-weight: 300;
    font-size: 1.4rem;
}
.input{
    width: 100%;
    height: 4rem;
    font-size: 1.5rem;
    border: 0px;
    background: #fff;
    box-shadow: 1px 2px 2px #888;
    border-radius: 4px;
    padding: 0 1rem;
}
.home_left img{
    width: 10rem;
    height: 10rem;
    border-radius: 10rem;
    margin: 1rem 0;
}

.home_right{
    width: 100%;
    height: 100%;
    padding: 1rem;
   display: grid;
   grid-template-columns: repeat(4, 1fr);
   grid-template-rows: repeat(1,15rem);
   grid-gap: 2px;
}
.disease_box{
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.9);
    padding: 1rem;
    border-radius: 0.5rem;
}
.disease_box img{
    width: 6rem;
    height: 6rem;
    border-radius: 6rem;
}
.disease_box h2{
    font-weight: 500;
    font-size: 1.8rem;
    margin: 2rem 0;
}
.disease_box p{
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
    color: #555;
    text-align: center;
    font-size: 1.4rem;
    line-height: 1.8rem;
}
.disease_box_1{
    grid-row: 1/2;
    grid-column: 1/2;
}
.disease_box_2{
    grid-row: 1/2;
    grid-column: 2/3;
}
.disease_box_3{
    grid-row: 1/2;
    grid-column: 3/4;
}
.disease_box_4{
    grid-row: 1/2;
    grid-column: 4/5;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20%;
}
.disease_box button {
    margin: 2rem 0;
    background: #348feb;
    border: 1px solid #fff;
    border-radius: 5px;
    padding: 1rem 2rem;
    color: #fff;
}