.container{
    width: 100%;
    min-height: 70vh;
    height: auto;
    background: url('../../Assets//bg-1.png') no-repeat;
    background-attachment: fixed;
    margin-top: 16vh;
}
.background{
    height: 70vh;
    width: 100%;
    background: rgba(255,255,255,0.5);
}
.dataContainer{
    background: rgba(255,255,255,0.5);
    padding-top: 5rem;
}
.headerButtons{
    height: 4rem;
    width: auto;
    margin: 1rem;
}
.headerButtons a{
    padding: 0.5rem 2rem;
    background: #fff;
    color: #222;
    height: auto;
    width: auto;
    text-decoration: none;
    margin: 0 1rem;
    box-shadow: 1px 1px 2px #aaa;
    border-radius: 4px;
}

.home_left{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 5rem;
    padding: 5rem;
   
}
.content{
    background: rgba(250, 252, 255, 0.8);
    width: 100%;
    padding: 5vh 5rem;
    border-radius: 1rem;
}
.home_left button{
    border: 1px solid #ddd;
    background: #fff;
    padding: 1rem 2rem;
    font-size: 1.5rem;
    margin: 1rem 0;
}
.form{
    width: 40rem;
    height:auto;
    padding: 2rem 5rem;
    background: rgba(239, 240, 241, 0.8);
    box-shadow: 1px 1px 1px #ccc;
    border-radius: 5px;
}

.inputContainer{
    margin: 1.5rem 0;  
}
.label{
    display: block;
    text-align: left;
    padding: 1px 0;
    font-weight: 300;
    font-size: 1.4rem;
}
.input{
    width: 100%;
    height: 4rem;
    font-size: 1.4rem;
    border: 0px;
    background: #fff;
    box-shadow: 1px 1px 1px rgb(170, 167, 167);
    border-radius: 4px;
    padding: 0 1rem;
}
.input[type="submit"]{
    background: #348feb;
    color: #fff;
}
.links  a{
    text-decoration: none;
    color: #348feb;
    font-size: 1.4rem;
    
}
.links  a:hover{
    font-size: 1.5rem;
    
}
.home_left img{
    width: 10rem;
    height: 10rem;
    border-radius: 10rem;
    margin: 1rem 0;
}

.select{
    display: inline-block;
    height: 4rem;
    padding: 1rem;
}
.option{
    height: 4rem;
    background: red;
}


